
import { Vue } from "vue-class-component";
import { User } from "../models/User";

export default class Register extends Vue {
  user: User = {
    username: "",
    password: ""
  };

  register(event: Event) {
    this.$store.dispatch("setUser", { ...this.user });
    this.$router.push("/");
    event.preventDefault();
  }
}
